@use './functions' as *;

.color-background {
  $self: &;
  &--red {
    background-color: get-color('red');
  }

  &--blue {
    background-color: get-color('blue');
  }

  &--green {
    background-color: get-color('green');
  }

  &--yellow {
    background-color: get-color('yellow');
  }

  &--pink {
    background-color: get-color('pink');
  }

  &--purple {
    background-color: get-color('purple');
  }

  &--teal {
    background-color: get-color('teal');
  }

  &--orange {
    background-color: get-color('orange');
  }

  &--fur {
    background-color: get-color('red', 4);
  }

  &--navy {
    background-color: get-color('blue', 8);
  }

  &--light-green {
    background-color: get-color('green', 4);
  }

  &--hot-pink {
    background-color: get-color('pink', 6);
  }

  &--brown {
    background-color: get-color('orange', 7);
  }

  &--forest {
    background-color: get-color('teal', 6);
  }

  &--violet {
    background-color: get-color('purple', 7);
  }

  &--cream {
    background-color: get-color('orange', 4);
  }

  &--none,
  &--grey {
    background-color: get-color('gray');
  }

  &--lime {
    background-color: get-color('lime');
  }

  &--black {
    background-color: get-color('black', 4);
  }

  &--white {
    background-color: get-color('white');
  }
}

.color-border {
  $self: &;
  &--red {
    border-color: get-color('red');
  }

  &--blue {
    border-color: get-color('blue');
  }

  &--green {
    border-color: get-color('green');
  }

  &--yellow {
    border-color: get-color('yellow');
  }

  &--pink {
    border-color: get-color('pink');
  }

  &--purple {
    border-color: get-color('purple');
  }

  &--teal {
    border-color: get-color('teal');
  }

  &--orange {
    border-color: get-color('orange');
  }

  &--fur {
    border-color: get-color('red', 4);
  }

  &--navy {
    border-color: get-color('blue', 8);
  }

  &--light-green {
    border-color: get-color('green', 4);
  }

  &--hot-pink {
    border-color: get-color('pink', 6);
  }

  &--brown {
    border-color: get-color('orange', 7);
  }

  &--forest {
    border-color: get-color('teal', 6);
  }

  &--violet {
    border-color: get-color('purple', 6);
  }

  &--cream {
    border-color: get-color('orange', 4);
  }

  &--none,
  &--grey {
    border-color: get-color('gray');
  }

  &--lime {
    border-color: get-color('lime');
  }

  &--black {
    border-color: get-color('black', 4);
  }

  &--white {
    border-color: get-color('white');
  }
}
