@use 'styles/sass' as *;

.background {
  background: linear-gradient(-45deg, #676666, #b33f62, #d39673, #9a5555, #b33f62);
  background-size: 1000% 1000%;
}

.h-items-selection {
  display: grid;
  grid-template-columns: repeat(4, auto);
  gap: get-unit(1);
  justify-items: center;
  background-color: get-color('black');
  border-radius: get-unit(2);
  padding: get-unit(2);

  &__button {
    border: 2px solid transparent;
    background: none;
    cursor: pointer;
    padding: 0;
  }
}

.h-items-crime-selection {
  display: flex;
  background-color: get-color('black');
  border-radius: get-unit(2);
  padding: get-unit(2);
  align-items: center;

  &__faded {
    opacity: 0.25;
  }
}

.h-scene-tiles-list {
  display: flex;
  overflow-y: auto;
}

.h-items-board {
  display: grid;
  grid-template-columns: repeat(6, auto);
  gap: get-unit(1);
  background-color: get-color('black');
  border-radius: get-unit(2);
  padding: get-unit(2);
}

.h-grouped-items-board {
  display: grid;
  grid-template-columns: repeat(2, auto);
  gap: get-unit(1);
  background-color: get-color('black');
  border-radius: get-unit(2);
  padding: get-unit(2);

  &__group {
    display: grid;
    grid-template-columns: repeat(4, auto);
    gap: get-unit(1);

    border: 2px solid get-color('brown');
    border-radius: get-unit(2);
    padding: get-unit(2);
  }
}

.h-crime {
  background-color: get-color('black');
  display: grid;
  grid-template-columns: [player] 2.5rem [info] auto [history] 28px [selections] 216px [done] 2.5rem;
  min-height: 200px;
  margin: get-unit(2);
  border-radius: $border-radius;
  color: get-color('white');
  min-width: 800px;

  &__player {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__player-name {
    display: block;
    transform: rotate(-90deg) translateX(-40%);
  }

  &__scenes {
    padding: get-unit(2);
  }

  &__history {
    border-left: 2px solid;

    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.h-crime-scene {
  display: grid;
  grid-template-columns: 3fr 7fr;
  margin-bottom: get-unit(1);

  &__label {
    white-space: nowrap;
    padding: get-unit(0 1);
    margin-right: get-unit(1);

    &--cause {
      background-color: get-color('violet', 6);
    }

    &--evidence {
      background-color: get-color('red', 6);
    }

    &--location {
      background-color: get-color('green', 6);
    }

    &--scene {
      background-color: get-color('orange', 6);
    }
  }

  &__value {
    padding: get-unit(0 1);
  }
}

.h-crime-selections {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto 1rem;
  grid-template-areas:
    'weapon evidence'
    'status status';
  padding: get-unit(1);

  &__status {
    grid-area: status;
    width: 100%;

    display: flex;
    justify-content: center;
  }
}

.crime-guess-status {
  padding: get-unit(1);
  display: flex;
  justify-content: center;
  align-items: center;

  &--LOCKED {
    color: get-color('gray', 5);
  }

  &--CORRECT {
    color: get-color('green', 5);
  }

  &--HALF {
    color: get-color('yellow', 7);
  }

  &--WRONG {
    color: get-color('red', 5);
  }

  &--WRONG_GROUP {
    color: get-color('purple', 6);
  }
}

.h-item-selector {
  width: 100px;
  margin-left: get-unit(1);
}

.h-players-cards {
  display: flex;
  margin-bottom: get-unit(2);

  &__seal {
    margin: 0 auto;
    height: 6ch;
    display: flex;
    align-items: center;
    box-sizing: border-box;
  }
}

.h-empty-space {
  width: 1px;
  height: 200px;
}
